import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/docs',
      name: 'docs',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: function () { 
        return import(/* webpackChunkName: "about" */ '../views/Docs.vue')
      }
    },
    {
        path: '/docs/icons',
        name: 'icons',
        component: () => import(/* webpackChunkName: "payment" */ '../views/docs/Icons.vue')
    },
    {
        path: '/docs/typography',
        name: 'typography',
        component: () => import(/* webpackChunkName: "payment" */ '../views/docs/Typography.vue')
    },
    {
        path: '/docs/buttons',
        name: 'buttons',
        component: () => import(/* webpackChunkName: "payment" */ '../views/docs/Buttons.vue')
    },
    {
        path: '/docs/popup',
        name: 'popup',
        component: () => import(/* webpackChunkName: "payment" */ '../views/docs/Popup.vue')
    },
    {
        path: '/docs/tooltip',
        name: 'tooltip',
        component: () => import(/* webpackChunkName: "payment" */ '../views/docs/Tooltip.vue')
    },
  ]
})
